.background{
    background-image: url('./worldmap.png');
    background-size: cover;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: baseline;
    justify-content: center;
  }
  
  .flag-game-container {
    background-color: #ececb0;
    border: 3px solid black;
    padding: 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
    height: 75vh; 
    width: 75vh;
    margin: 0 auto;
  }
  
  .flag-game-container img {
    width: 98%;
    max-width: 400px;
    margin-bottom: 20px;
    border: 3px solid black;
  }
  
  .flag-game-container form {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .flag-game-container form input[type="text"] {
    padding: 10px;
    margin-right: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .flag-game-container button {
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-left: 10px;
  }
  
  .flag-game-container button[type="submit"] {
    background-color: #28a745;
  }
  
  .flag-game-container button[type="submit"]:hover {
    background-color: #218838;
  }
  
  .flag-game-container button[type="button"] {
    background-color: #dc3545;
  }
  
  .flag-game-container button[type="button"]:hover {
    background-color: #c82333;
  }
  
  #timedButton{
    background-color: #4b35dc;
  }
  #timedButton:hover {
    background-color: #351ae0;
  }
  
  .flag-game-container h3 {
    font-size: 20px;
    margin-bottom: 10px;
  }
  
  .flag-game-container p {
    font-size: 16px;
    margin-bottom: 5px;
  }
  
  .game-wrapper {
    display: flex;
  }
  
  .scoreAndTime {
    display: flex;
  }
  .score-section {
    margin-top: 100px;
    margin-left: 20px;
  }
  
  .Timecard {
    display: flex;
    position: absolute;
    bottom: 40vh;
    color: red;
  }