.modal {
    background-image: url('./flagbackground.png');
      background-size: cover;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.5);
      display: flex;
      align-items: baseline;
      justify-content: center;
    }
    
    .modal-content {
      margin-top: 10;
      background-color: #fff;
      padding: 20px;
      border-radius: 4px;
      text-align: center;
    }
    
    .modal-content h2 {
      font-size: 24px;
      margin-bottom: 10px;
    }
    
    .modal-content p {
      font-size: 16px;
      margin-bottom: 20px;
    }
    
    .modal-content button {
      padding: 10px 20px;
      background-color: #007bff;
      color: #fff;
      border: none;
      border-radius: 4px;
      cursor: pointer;
    }
    
    .modal-content button:hover {
      background-color: #0056b3;
    }